import React, { useState } from 'react';
import { Box, CircularProgress, Grid, Typography } from '@mui/material';
import axios from 'axios';
import CustomSnackBar, { CustomSnackBarState } from '../../components/CustomSnackBar';

const Home: React.FC = () => {
    const [syncing, setSyncing] = useState(false);
    const [snackBar, setSnackBar] = useState<CustomSnackBarState>({
        open: false,
        type: "success",
        message: ""
    })

    const login = () => {
        axios.get("http://127.0.0.1:3000/api/portal").then(res => {

        }).catch(err => {

        })
    }

    const syncData = async () => {
        setSyncing(true)
        let res1 = await axios.get("http://127.0.0.1:3000/api/portal/get-access-token")
        let res2 = await axios.get("http://127.0.0.1:3000/api/portal/sync-data", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem('accessToken')}`
            }
        
        })
        setSyncing(false)
        setSnackBar({
            type: "success",
            message: "Data successfully synced to the server.",
            open: true
        })
    }



    return (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '60vh' }}>

            <Grid
                container
                spacing={2}
                justifyContent="center"
                alignItems="center">
                <Grid item xs={2}>
                    <Box onClick={login} sx={{
                        backgroundColor: 'primary.main',
                        p: 2,
                        borderRadius: 2,
                        cursor: 'pointer', // Change cursor to indicate interactivity
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}>
                        <Typography variant="h5" color="white">
                            Login
                        </Typography>
                    </Box>
                </Grid>
                <Grid item xs={2} style={{
                    cursor: "pointer"
                }}>
                    <Box                        
                        sx={{
                            backgroundColor: 'error.main',
                            p: 2,
                            borderRadius: 2,
                            cursor: 'pointer', // Change cursor to indicate interactivity
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                        }}
                        onClick={syncData}
                    >

                        {syncing ? (
                            <CircularProgress size="24px" sx={{ color: 'white' }} /> // Show circular progress during loading
                        ) : (
                            <Typography variant="h5" color="white">
                                Sync Data
                            </Typography>
                        )}

                    </Box>
                </Grid>
            </Grid>
            <CustomSnackBar
                open={snackBar.open}
                type={snackBar.type}
                message={snackBar.message}
                handleClose={() => {
                    setSnackBar(prevState => ({
                        ...prevState,
                        open: false
                    }))
                }} />
        </Box>
    )
}

export default Home;