import { Alert, AlertColor, Snackbar } from '@mui/material'
import React from 'react'


interface CustomSnackBarProps {
    open: boolean,
    message: String,
    type: AlertColor,
    handleClose: () => void
}

export interface CustomSnackBarState {
    open: boolean,
    message: String,
    type: AlertColor,
}

const CustomSnackBar: React.FC<CustomSnackBarProps> = (props) => {
    return (
        <Snackbar
                open={props.open}
                anchorOrigin={{
                    horizontal: "right",
                    vertical: "bottom"
                }}
                autoHideDuration={3000}
                onClose={props.handleClose}
                message="Note archived"
            >
                <Alert onClose={props.handleClose} severity={props.type} sx={{ width: '100%' }}>
                    {props.message}
                </Alert>
            </Snackbar>
    )
}

export default CustomSnackBar