import Axios, { AxiosInstance } from 'axios'
import { NavigateFunction } from 'react-router-dom'

export enum APiType {
    GSTAPI,
    AUTHAPI
}

export default class ClientApi {
    client: AxiosInstance
    navigate: NavigateFunction
    constructor(navigate: NavigateFunction, apiType: APiType, apiUrl: string = `http://localhost:3000`) {
        if (apiType === APiType.GSTAPI) {
            this.navigate = navigate
            let token = localStorage.getItem("accessToken")
            this.client = Axios.create({
                baseURL: apiUrl, //`https://apiv2.truedreem.com`,
                headers: {
                    'Authorization': token ? `Bearer ${token}` : "",
                    'Content-Type': 'application/json'
                }
            })

            this.client.interceptors.response.use(
                response => response,
                error => {
                    console.log(error.response.status)
                    const { status } = error.response;
                    console.log(status)
                    if (401 === status || 403 === status) {
                        localStorage.removeItem('accessToken')
                        localStorage.removeItem('username')
                        navigate("/", {
                            replace: true
                        })
                    }
                    return Promise.reject(error)
                }
            )
        } else {
            this.navigate = navigate
            let token = localStorage.getItem("accessToken")
            this.client = Axios.create({
                baseURL: `http://localhost:3000`, //`https://apiv2.truedreem.com`,
                headers: {
                    'Authorization': token ? `Bearer ${token}` : "",
                    'Content-Type': 'application/json'
                }
            })

            this.client.interceptors.response.use(
                response => response,
                error => {
                    console.log(error.response.status)
                    const { status } = error.response;
                    console.log(status)
                    if (401 === status || 403 === status) {
                        localStorage.removeItem('accessToken')
                        localStorage.removeItem('username')
                        navigate("/", {
                            replace: true
                        })
                    }
                    return Promise.reject(error)
                }
            )
        }
    }
}