import { NavigateFunction } from "react-router-dom";
import ClientApi, { APiType } from "./base";

export default class GstApi extends ClientApi {
    constructor(navigate: NavigateFunction) {
        super(navigate, APiType.GSTAPI)
    }

    ping = () => {
        return this.client.post("/api/idm/ping")
    }

    userList = () => {
        return this.client.get("/api/users/")
    }

    login = (data: any) => {
        return this.client.post("/api/idm/login", data)
    }

    getGstNumbers = () => {
        return this.client.get('/api/gst/')
    }

    getGstNumbersWithData = (data: any) => {
        return this.client.post('/api/gst-status/get-gst-status', data)
    }

    addGst = (data: any) => {
        return this.client.post('/api/gst/', data)
    }

    updateGst = (id: any, data: any) => {
        return this.client.put(`/api/gst/${id}`, data)
    }

    deleetGst = (id: any) => {
        return this.client.delete(`/api/gst/${id}`)
    }

    updateGstStatus = (data: any) => {
        return this.client.post(`/api/gst-status/update-gst-status`, data)
    }
}