import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import GstApi from '../../services/gstApi';
import { useState } from 'react';
import { Alert, CircularProgress, Snackbar } from '@mui/material';

export default function Login() {
    const navigate = useNavigate();
    const api = React.useMemo(() => new GstApi(navigate), [])
    const [loading, setLoading] = useState(true)
    const [loginLoading, setLoginLoading] = useState(false)
    const [open, setOpen] = useState(false)

    const handleClose = () => {
        setOpen(false)
    }

    React.useEffect(() => {
        let token = localStorage.getItem('accessToken')
        if (token) {
            api.ping().then(res => {
                navigate("/portal", {
                    replace: true
                })
            }).catch(err => {
                setLoading(false)
            })
        } else {
            setLoading(false)
        }
    }, [])

    const addFooters = (doc: any) => {
        const pageCount = doc.internal.getNumberOfPages()

        doc.setFontSize(8)
        for (var i = 1; i <= pageCount; i++) {
            doc.setPage(i)
            doc.text('Page ' + String(i) + ' of ' + String(pageCount), doc.internal.pageSize.width / 2, 291, {
                align: 'center'
            })
            doc.setFont('helvetica', 'italic')
            // doc.setLineWidth(5.0); 
            doc.line(0, 286, 560, 286);
            doc.setFontSize(10)
            doc.text('Megha Travels', 15, 291)
            doc.text("TEST", 350, 291)
            doc.text("TEST 2", 320, 291)
        }
    }

    const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        if (loginLoading) return
        setLoginLoading(true)
        const data = new FormData(event.currentTarget);
        console.log({
            email: data.get('email'),
            password: data.get('password'),
        });
        api.login({
            username: data.get('email'),
            password: data.get('password')
        }).then(res => {
            localStorage.setItem("accessToken", res.data.access_token)
            localStorage.setItem("username", res.data.user.username)
            localStorage.setItem("userRole", res.data.userRole)
            navigate("/portal")
        }).catch(err => {
            setLoginLoading(false)
            setOpen(true)
        })
    };

    return (
        <Container component="main" maxWidth="xs">
            <Box
                sx={{
                    marginTop: 12,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                {loading ?
                    <CircularProgress />
                    :
                    <React.Fragment>
                        <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                            <LockOutlinedIcon />
                        </Avatar>
                        <Typography component="h1" variant="h5">
                            Sign in
                        </Typography>
                        <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                id="email"
                                label="Username"
                                name="email"
                                autoComplete="email"
                                autoFocus
                            />
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                name="password"
                                label="Password"
                                type="password"
                                id="password"
                                autoComplete="current-password"
                            />
                            <Button
                                type="submit"
                                fullWidth
                                variant="contained"
                                sx={{ mt: 3, mb: 2 }}
                            >
                                {loginLoading ? <CircularProgress
                                    color='inherit'
                                    size={24}
                                /> : "Sign In"}
                            </Button>

                        </Box>
                    </React.Fragment>
                }
            </Box>
            <Snackbar
                open={open}
                anchorOrigin={{
                    horizontal: "right",
                    vertical: "bottom"
                }}
                autoHideDuration={3000}
                onClose={handleClose}
                message="Note archived"
            >
                <Alert onClose={handleClose} severity="error" sx={{ width: '100%' }}>
                    Invalid login details. Try again later.
                </Alert>
            </Snackbar>
        </Container >
    );
}